<template>
  <el-container>
    <v-chart :options="option"/>
  </el-container>
</template>
<script>
export default {
  data () {
    return {
      option:  {
        title: {
                text: 'ECharts 入门示例'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: [5, 20, 34, 10, 10, 20,]
            }]
      }
    }
  }
}
</script>
<style>
</style>


